                    validacion = false
                }

                if(this.qna_desde.length<6){
                    this.mensajes_des.push({des:'La quincena de asignacion debe contener 6 digitos'})
                    validacion = false
                }
                
                if(parseInt(this.qna_desde.substring(4))>24 ){
                    this.mensajes_des.push({des:'El numero maximo de quincena es de 24'})
                    validacion = false
                }
                */
                return validacion
               
